






import { Component, Vue, Prop } from 'vue-property-decorator'

@Component({
  props: {
    propValue: {
      type: String,
      required: true
    }
  }
})
export default class UmrButton extends Vue {
  @Prop({ default: 'plain' })
  type?: 'plain' | 'disabled' | 'round'
  get computedType() {
    return `is-${this.type}`
  }
}
